import {Delimiter, EMPTY_STRING} from '../constant/Constant'

export const Base64 = {
    encode: (input: string) => {
        try {
            return btoa(input)
        } catch (e) {
            return EMPTY_STRING
        }
    },
    encodeUrlSafe: (input: string) => {
        let encoded = Base64.encode(input)
        return encoded.replace(/\+/g, Delimiter.HYPHEN) // + to -
            .replace(/\//g, Delimiter.UNDERSCORE) // / to _
            // remove padding 제거 (btoa 와 동일값 보장)
            // .replace(/=+$/, '') // remove padding
    },
    decode: (input: string) => {
        try {
            return atob(input)
        } catch (e) {
            return EMPTY_STRING
        }
    },
    decodeUrlSafe: (input: string) => {
        let replaced = input.replace(/-/g, Delimiter.PLUS) // - to +
            .replace(/_/g, Delimiter.SLASH) // _ to /

        return Base64.decode(replaced)
    }
}

export const Url = {
    encode: (input: string) => {
        return encodeURIComponent(input)
    },
    decode: (input: string) => {
        return decodeURIComponent(input)
    },
}