'use client'

import {useState} from 'react'
import * as R from 'remeda'
import {EMPTY_OBJECT, EMPTY_STRING, NOT_APPLICABLE} from '@nyx/kr-common/constant/Constant'
import {Base64, Url} from 'src/kr/common/util/Encoder'

type Encoded = {
    urlEncoded: string,
    urlDecoded: string,
    base64Encoded: string,
    base64Decoded: string,
    base64UrlEncoded: string,
    base64UrlDecoded: string,
}

export default function KrEncoder() {
    const [input, setInput] = useState<string>(EMPTY_STRING)
    const [encoded, setEncoded] = useState<Record<string, string>>(EMPTY_OBJECT)
    const [decoded, setDecoded] = useState<Record<string, string>>(EMPTY_OBJECT)

    // encode
    const handleEncodeButtonClick = () => {
        setEncoded({
            urlEncoded: Url.encode(input),
            base64Encoded: Base64.encode(input),
            base64UrlEncoded: Base64.encodeUrlSafe(input)
        })
        setDecoded(EMPTY_OBJECT)
    }

    // decode
    const handleDecodeButtonClick = () => {
        setDecoded({
            urlDecoded: Url.decode(input),
            base64Decoded: Base64.decode(input),
            base64UrlDecoded: Base64.decodeUrlSafe(input)
        })
        setEncoded(EMPTY_OBJECT)
    }

    return (
        <div style={{padding: "20px", margin: "0 auto"}}>
            <h1>Encoder/Decoder (Base64, URL)</h1>
            <textarea
                value={input}
                onChange={(e) => setInput(e.target.value.trim())}
                rows={4}
                placeholder="Enter text to encode or decode"
                style={{width: "100%", padding: "10px", marginBottom: "10px"}}
            />
            <div>
                <button onClick={handleEncodeButtonClick} style={{margin: "10px"}}>Encode</button>
                <button onClick={handleDecodeButtonClick}>Decode</button>
            </div>
            {/* Encoding */}
            {!R.isEmpty(encoded) && (
                <div style={{marginTop: "20px"}}>
                    <h2>Encoded Result</h2>
                    <pre style={{backgroundColor: "#f0f0f0", padding: "10px"}}>
                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        Base64 Encoded:
                      </strong>
                        {encoded.base64Encoded}
                        <br/><br/>
                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        Base64 URL-Safe Encoded:
                      </strong>
                        {encoded.base64UrlEncoded}
                        <br/><br/>
                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        URL Encoded:
                      </strong>
                        {encoded.urlEncoded}
                    </pre>
                </div>
            )}

            {/* Decoding */}
            {!R.isEmpty(decoded) && (
                <div style={{marginTop: "20px"}}>
                    <h2>Decoded Result</h2>
                    <pre style={{backgroundColor: "#f0f0f0", padding: "10px"}}>
                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        Base64 Decoded:
                      </strong>
                        {R.isEmpty(decoded.base64Decoded) ? NOT_APPLICABLE : decoded.base64Decoded}
                        <br/><br/>
                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        Base64 URL-Safe Decoded:
                      </strong>
                        {R.isEmpty(decoded.base64UrlDecoded) ? NOT_APPLICABLE : decoded.base64UrlDecoded}
                        <br/><br/>
                      <strong style={{color: "#333", display: "block", marginBottom: "8px"}}>
                        URL Decoded:
                      </strong>
                        {R.isEmpty(decoded.urlDecoded) ? NOT_APPLICABLE : decoded.urlDecoded}
                    </pre>
                </div>
            )}
        </div>
    )
}